.sort {
  display: flex;
  padding-top: 2px;
  margin-bottom: 23px;
}

.sort__title {
  margin: 0;
  margin-right: 25px;

  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.05em;
  color: #000000;
}

.sort__options {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;

  button {
    display: block;
    padding: 0;

    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.05em;
    color: #585757;

    border: none;

    background-color: transparent;

    cursor: pointer;

    &:hover,
    &:focus {
      color: #000000;
    }

    &:active {
      opacity: 0.8;
    }
  }
}

.sort__item {
  margin-right: 36px;

  &:last-child {
    margin-right: 0;
  }
}

.sort__view {
  @include list-reset;

  display: flex;
  justify-content: space-between;
  width: 58px;
  margin-left: auto;
  margin-right: 8px;

  button {
    display: block;
    padding: 0;

    color: #6c6c6c;

    border: none;

    background-color: transparent;

    cursor: pointer;

    &:hover,
    &:focus {
      color: #000000;
    }

    &:active {
      opacity: 0.8;
    }
  }
}
